}*/
	/**/
	/*.mint-tab-item-label{
		font-size:20px;
	}
	.mint-navbar a{
	    color: #A8A8A8;
	    
	}
	.mint-navbar .mint-tab-item.is-selected {
	    border-bottom: 2px solid #D71E25;
	    color: #D71E25;
	    margin-bottom: -3px;
	}*/
</style>


